import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController,
  ModalController
} from "ionic-angular";
import { ProgramViewModel } from "../../Model/ViewModel/ProgramViewModel";
import { GlobalComponent } from "../../components/global/global";

/**
 * Generated class for the programModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-batch-program-add-edit-modal",
  templateUrl: "batch-program-add-edit-modal.html"
})
export class BatchProgramAddEditModalPage {
  item: ProgramViewModel;
  mode: string;
  CanEditBatch:boolean;
  title:string;
  sql_help:string=`
  異常變數說明：
  {batch_log_seq}=批次紀錄序號,
  {parent_batch_log_seq}=上層批次紀錄序號,
  {exec_log_seq}=轉檔序號,
  {job_step_log_seq}=執行作業步驟紀錄序號,
  {filename}=檔名,
  {schema}=結構描述,
  {tablename}=匯入資料表名稱,
  {ext_column}=異常關鍵字欄位,
  {JOB01},{JOB02},{JOB03}=作業迴圈參數,
  {LC01},{LC02},{LC03}=內部檔案迴圈參數,
  警訊變數說明：
  {batch_log_seq}=批次紀錄序號,
  {parent_batch_log_seq}=上層批次紀錄序號,
  {exec_log_seq}=轉檔序號,
  {job_step_log_seq}=執行作業步驟紀錄序號,
  {filename}=檔名,
  {schema}=結構描述,
  {tablename}=匯入資料表名稱,
  {ext_column}=異常關鍵字欄位,
  {src_path_file}=來源路徑檔名,
  {src_file_path}=來源檔案路徑,
  {bk_path_file}=備份路徑檔名,
  {bk_file_path}=備份檔案路徑,
  {file_size}=檔案大小,
  {file_ext}=附檔名,
  @bindarydata=二進位檔案內容,
  {JOB01},{JOB02},{JOB03}=作業迴圈參數,
  {LC01},{LC02},{LC03}=內部檔案迴圈參數,

  檔案匯入前產生的暫存資料表sql語法:
  declare @schemaname varchar(30)='comm',@tablename varchar(50)='tb_code'
SELECT [comm].[fn_gen_temp_sql] (
@schemaname,@tablename)

關聯檢核範例:
  declare @column_id int,@columnname varchar(50)='code_desc'
  set @column_id=[comm].[fn_get_column_id] ('{schema}','{tablename}',@columnname)
      delete  #{tablename}
    OUTPUT   
         deleted.exec_log_seq
    ,deleted.line_no
    ,@column_id --column_id
    ,@columnname  --columnname
    ,deleted.code_desc +'<>代碼描述:'+ TB1.code_desc --err_content
    ,'003|6'  --err_key 外部索引鍵檢核異常
    ,'{tablename}' --tablename
    ,'{ext_column}' --ext_column
    ,deleted.{ext_column} --ext_column_value
    into comm.tb_err_log (exec_log_seq,line_no,column_id,columnname,err_content,err_key,tablename,ext_column,ext_column_value)
     from #{tablename} as TP 
     INNER JOIN  comm.tb_code TB1
    ON TP.code_key=TB1.code_key
    where TP.code_desc<>TB1.code_desc
  警訊檢核範例:
    declare @exec_log_seq bigint={exec_log_seq} 
    declare @column_id int,@columnname varchar(50)='code_desc'
    set @column_id=[comm].[fn_get_column_id] ('{schema}','{tablename}',@columnname)
  	insert into comm.tb_err_log (exec_log_seq,line_no,column_id,columnname,err_content,err_key,tablename,ext_column,ext_column_value)	
	select TP.file_bat_seq exec_log_seq
	,TP.line_no
	,@column_id --column_id
	,@columnname  --columnname
	,TP.code_desc +'<>共用代碼檔:'+ TB1.code_desc --err_content
	,'044|A03'  --err_key 分行代號與保單主檔內容不符
	,'{tablename}' --tablename
	,'code_key' --ext_column
	,TP.code_key --ext_column_value
	 from {schema}.{tablename} as TP 
	 INNER JOIN comm.tb_code TB1
	ON TP.code_key=TB1.code_key
	where TP.file_bat_seq=@exec_log_seq and TP.code_desc<>TB1.code_desc
  `;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public modalCtrl: ModalController,
    public global: GlobalComponent,
  ) {
    this.mode = navParams.data.mode;
    this.title="批次SQL程式";
    this.item = new ProgramViewModel();
    if(navParams.data.change_mode==="c")
      this.item.program_no = "auto";
    else
      this.item.program_no = navParams.data.item.program_no;
    this.item.program_desc = navParams.data.item.program_desc;
    this.item.program_name = navParams.data.item.program_name;
    this.item.bus_type_key = navParams.data.item.bus_type_key;
    this.item.bus_type = navParams.data.item.bus_type;
    this.item.program_type_key = navParams.data.item.program_type_key;
    this.item.program_type_seq = navParams.data.item.program_type_seq;
    this.item.program_path_key = navParams.data.item.program_path_key;
    this.item.master_program_no = navParams.data.item.master_program_no;
    this.item.parent_program_no = navParams.data.item.parent_program_no;
    this.item.menu_key = navParams.data.item.menu_key;
    this.item.audit_flag = navParams.data.item.audit_flag;
    this.item.creator = navParams.data.item.creator;
    this.item.create_time = navParams.data.item.create_time;
    this.item.modifier = navParams.data.item.modifier;
    this.item.last_update_time = navParams.data.item.last_update_time;
    this.item.is_active = navParams.data.item.is_active;
    this.item.sql_statement = navParams.data.item.sql_statement;
    this.CanEditBatch=navParams.data.CanEditBatch;

    console.log(this.CanEditBatch);
  }
  SelectBusType() {
    let modal = this.modalCtrl.create("CodeSelectModalPage", {
      select_key: this.item.bus_type_key,
      code_type: "022"
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.bus_type_key = select_data.code_key;
      this.item.bus_type = select_data.code_desc;
    });

    modal.present();
  }
  Help() {

    this.global.createLoader();

    this.global.loading.present().then(() => {
       this.global.showMessage("SQL語法說明",this.sql_help);

    });

  }
  // SelectProgram() {
  //   let modal = this.modalCtrl.create("BatchProgramSelectModalPage", {
  //     select_item: this.item.program_no,
  //   });
  //   modal.onDidDismiss(select_data => {
  //     if (select_data == null) return;
  //     this.item.program_no = select_data.program_no;
  //     this.item.program_desc = select_data.program_desc;
  //   });

  //   modal.present();
  // }
  Save() {
    this.item.program_name=this.item.program_desc;

    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad ProgramModalPage");
  }
}



